//Import ALL extends
@import "animation";

%fontAwesome {
  font-weight: 900;

  @include font("Font Awesome\ 5 Pro");
}

%borderTop {
  border-top: 1px solid rgba($white, 0.5);
  padding-top: 7.5px;
}

%inputFullHeight {
  form {
    height: 100%;
    overflow: hidden;

    input {
      height: 100%;
    }
  }
}

%inputFullWidth {
  form {
    width: 100%;
    overflow: hidden;

    input {
      width: 100%;
    }
  }
}

%absoluteFill {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

%bgImage {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

%disabled {
  pointer-events: none;
  opacity: 0.2;
}